
/* Обнуление стилей */

* {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
 
  
    &:before,
    &:after {
      box-sizing: border-box;
    }
  }
  
  :focus,
  :active {
    outline: none;
  }
  
 a {
   text-decoration: unset;
   color: inherit;
 }
 img {
   max-width: 100%;
 }
 
 button {
   all: initial;
 }
 nav {
   display: flex;
 }
 li, ul {
  list-style: none;
 }